
"use client";

import { Button } from "@/components/ui/button";
import { Rss } from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { useLanguage } from "@/context/language-context";

export default function BlogPage() {
  const { translations } = useLanguage();
  const blogPosts = translations.blog.posts;

  return (
    <section id="blog" className="py-16 sm:py-24 h-full">
      <div className="container">
        <div className="flex items-center gap-4 mb-12">
            <Rss className="w-8 h-8 text-primary" />
            <h2 className="font-headline text-3xl sm:text-4xl font-bold">{translations.blog.title}</h2>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {blogPosts.map((post) => (
            <Card key={post.title} className="flex flex-col">
              <CardHeader>
                <CardTitle className="font-headline text-xl">{post.title}</CardTitle>
                <p className="text-sm text-muted-foreground">{post.date}</p>
              </CardHeader>
              <CardContent className="flex-grow">
                <CardDescription>{post.description}</CardDescription>
              </CardContent>
              <CardContent>
                  <Button asChild variant="outline">
                    <a href={post.url}>{translations.blog.read_more}</a>
                  </Button>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
